export const target = (buckType, properties) => `${buckType} (\n${properties}\n)`;
export const stringProperty= (name, value) => `  ${name} = '${value}'`;
export const arrayProperty = (name, value) => {
  let values = value.map(v => `    '${v}'`).join(',\n');
  return `  ${name} = [\n${values}\n  ]`;
};